import React, {Component} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import AddToDo from "./AddToDo";

export default class ToDoList extends Component {
    state = {
        todos:[
            {Id:'1',Title:'Push code to github', Status:"Done"},
            {Id:'2',Title:'Email to clien', Status:"Pending"}
        ]
    };
    deleteToDo = (todo) =>{
        const filteredItems = this.state.todos.filter(x=> x.Id !== todo.Id);
        this.setState({
            todos: filteredItems
        });
    };
    editToDo= (x) => {
        this.setState(state => ({
            todos: state.todos.map(todo => {
                if(todo.Id === x.Id){
                    return{
                        ...todo,
                        Status: todo.Status=== "Done" ? "Pending": "Done"
                    }
                } else{
                    return todo;
                }
            })
        }));
    }
    addToDo = (todo) =>{
        this.setState({
            todos: [...this.state.todos,todo] 
        });
    };
    render(){
        return (
            <div>
                <AddToDo onAdd={this.addToDo}></AddToDo>
                <h1>ToDoList</h1>
                <table className="table" >
                    <thead>
                        <tr>
                            <th>Id</th>
                            <th>Title</th>
                            <th>Status</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.todos.map(x=>{
                            return(
                                <tr>
                                    <td>{x.Id}</td>
                                    <td>{x.Title}</td>
                                    <td style={{color: x.Status === 'Done' ? 'green' :'red' }}>{x.Status}</td>
                                    <td>
                                        <button className='btn btn-primary' onClick={() =>this.deleteToDo(x)}>
                                            <span>
                                                <FontAwesomeIcon icon="trash"></FontAwesomeIcon>
                                            </span>
                                        </button>
                                    </td>
                                    <td>
                                        <button>
                                            <span>
                                                <FontAwesomeIcon icon="edit" onClick={() =>this.editToDo(x)}></FontAwesomeIcon>
                                            </span>
                                        </button>
                                    </td>
                                </tr>
                            );
                        })}
                    </tbody>
                </table>
            </div>
        );
    }
}